h1{
  text-align: center;
}
hr{
  width: 180px;
  text-align: center;
  height: 5px;
  background-color: #000;
  border: none;
  border-radius: 5px;
}
.sites-container{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.box{
  border: 5px solid #777;
  margin: 20px ;
  border-radius: 3px;
  width: 350px;
  box-shadow: 1px 2px 3px #999;
}
.site-img{
  width: 100%;
  height: 300;
}
.site-name{
  text-align: center;
  font-size: larger;
  font-weight: bold;
  text-decoration: none;
  display: block;
  color: #333;
}